.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background-image: linear-gradient(180deg, #F6F4FF 8.65%, rgba(246, 237, 255, 0.5) 29.48%, rgba(227, 214, 240, 0.48) 55.52%, rgba(255, 255, 255, 0) 100%);
}

.fade-in {
  animation: fadeIn 0.7s ease-in-out;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate3d(0, -25%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fondo_secciones {
  background-color: #F6F4FF;
}

.gradiente_verde {
  background: linear-gradient(180deg, rgba(233, 251, 247, 0.12) 6.04%, rgba(196, 248, 235, 0.46) 55.65%, rgba(233, 251, 247, 0) 100%);
  }

