/* tab bar */

.tab-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
}

.tab-bar div {
  margin: 0 10px;
  padding: 10px;
  cursor: pointer;
  font-size: 22px;
}

.tab-content {
  margin-top: 20px;
}

@media (max-width: 576px) {
  .tab-item {
    padding: 10px;
    width: 100%;
    text-align: center;
  }
  .tab-item.active {
    margin-right: 0;
  }
}

.tab-item {
  position: relative;
  display: inline-block;
  padding: 0 8px;
  cursor: pointer;
  font-size: 22px;
}

.tab-item.active {
  font-weight: 600;
}

.tab-item::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 3px;
  background-color: #7858DD;
  transition: width 0.3s ease-in-out;
}

.tab-item:hover::after {
  width: calc(100% - 16px);
}

.tab-item.active::after {
  width: calc(100% - 16px);
}

/* noticias */

.cajanoticias{
  height: 696px;
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 150px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 1;
  border-radius: 46px;
}

.imagennoticia{
  height: 500px;
  position: relative;
  left: 50px;
  z-index: 2;
  border-radius: 42px;
}

.leermas{
  font-size: 36px;
  text-decoration: none;
  display: block;
  text-align: right;
  margin-right: 40px;
  position: absolute;
  bottom: 30px;
  right: 40px;
}

@media (max-width: 768px) {
  .container {
    max-width: 100%;
  }
  .imagennoticia {
    position: relative;
    left: 0;
    margin-right: 0;
    border-radius: 42px;
    height: auto;
    z-index: 1;
  }
  .cajanoticias {
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 46px;
    height: auto;
    z-index: 2;
    margin-top: -90px;
  }
}
